import React, { useState } from "react";
import styled from "styled-components";

const CounterContainer = styled.div`
    display: flex;
    align-items: center;
    gap: 10px;
    button {
        font-size: 20px;
        background: white;
        border: 1px solid #ddd;
        border-radius: 4px;
        &:hover {
            background: #ffb935;
            cursor: pointer !important;
        }
    }
`
export const Counter = ({ defaultCount, onChange }) => {
    const [count, setCount] = useState(defaultCount);
  
    const onIncrease = () => {
      if (count !== 5) {
        setCount(count + 1);
        onChange(count + 1);
      }
    };
  
    const onDecrease = () => {
      if (count !== 0) {
        setCount(count - 1);
        onChange(count - 1);
      }
    };
  
    return (
      <CounterContainer>
        <button onClick={onDecrease}>-</button>
        <span>{count}</span>
        <button onClick={onIncrease}>+</button>
      </CounterContainer>
    );
  };